/*
    Author: Joren Thijs
*/
body {
  font-family: 'Muli', sans-serif;
  font-weight: 300;
  display: grid;
  grid-template: ". navbar ." minmax(2em, 1fr) ". container ." auto/minmax(3.6em, 1fr) auto minmax(3.6em, 1fr); }

.container {
  grid-area: container;
  display: grid;
  grid-template: 'content' auto 'image' auto / auto; }
  @media (min-width: 800px) {
    .container {
      margin-top: 7.5em;
      grid-template: "content . image" auto/auto minmax(0.5em, 15em) auto; } }

.clickable {
  cursor: pointer; }

a {
  text-decoration: none;
  color: inherit; }
  a:visited {
    color: inherit; }
  a:hover {
    color: #1F6AFF; }
  a:active {
    color: inherit; }

.navigation {
  grid-area: navbar;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2.7em;
  margin-left: -1.4em;
  margin-right: -1.5em;
  margin-bottom: 1.3em;
  max-height: 2em; }
  @media (min-width: 800px) {
    .navigation {
      height: 2em; } }
  @media (min-width: 800px) {
    .navigation .hamburger {
      display: none; } }
  .navigation .nav-items {
    position: absolute;
    top: 3em;
    left: 50%;
    margin-left: -6em;
    transform: scale(1, 0);
    transform-origin: top;
    transition: transform 150ms ease-in-out; }
    @media (min-width: 800px) {
      .navigation .nav-items {
        position: relative;
        top: unset;
        left: unset;
        margin-left: unset;
        transform: unset;
        transition: unset;
        display: flex;
        flex-direction: row;
        justify-content: flex-start; } }
    .navigation .nav-items .nav-item {
      text-align: center;
      list-style: none;
      margin: 0.4em auto;
      font-weight: 300;
      font-size: 2em;
      position: relative;
      opacity: 0; }
      @media (min-width: 800px) {
        .navigation .nav-items .nav-item {
          opacity: unset;
          margin-right: 1.5em;
          font-size: 1.5em; } }
    .navigation .nav-items .nav-item.active-link::after {
      position: absolute;
      bottom: -0.1em;
      left: 0;
      content: '';
      width: 100%;
      height: 1.5px;
      border-radius: 3px;
      background: #1F6AFF;
      transform: scale(0, 1);
      transform-origin: top;
      transition: transform 300ms ease-in 200ms; }
      @media (min-width: 800px) {
        .navigation .nav-items .nav-item.active-link::after {
          transform: scale(1, 1); } }
  .navigation .github-mark {
    min-width: 35px;
    filter: invert(42%) sepia(70%) saturate(5682%) hue-rotate(213deg) brightness(100%) contrast(103%); }

.navigation.expanded {
  margin-bottom: 13em; }
  @media (min-width: 800px) {
    .navigation.expanded {
      margin-bottom: unset; } }
  .navigation.expanded .nav-items {
    transform: scale(1, 1); }
    .navigation.expanded .nav-items .nav-item {
      opacity: 1;
      transition: opacity 150ms ease-in 250ms; }
    .navigation.expanded .nav-items .nav-item.active-link::after {
      transform: scale(1, 1); }

.flex-spacer {
  flex: 1; }

.max-width-600 {
  max-width: 600px; }

.max-width-150 {
  max-width: 150px; }

.max-width-250 {
  max-width: 7em; }

.left-column {
  grid-area: content; }
  @media (min-width: 800px) {
    .left-column {
      max-width: 23em;
      display: flex;
      flex-direction: column;
      align-items: initial;
      justify-content: flex-start; } }

h1, h2 {
  margin: 0.9em auto;
  max-width: 520px; }
  @media (min-width: 800px) {
    h1, h2 {
      margin: 0.6em auto; } }

.main-title {
  color: #424242;
  font-size: 1.6em;
  line-height: 1.5em; }

.sub-title {
  color: #6D6A6A;
  font-size: 1.3em; }
  @media (min-width: 800px) {
    .sub-title {
      margin-left: 0; } }

.introduction {
  color: #6D6A6A;
  font-size: 1em;
  margin-bottom: 2.5em; }

.learn-more {
  color: #FD2DEE;
  white-space: nowrap; }
  .learn-more:hover {
    color: #1F6AFF; }

.primary-action-large {
  color: #FFFFFF;
  font-size: 1.8em;
  text-align: center;
  margin: auto;
  max-width: 520px;
  line-height: 1.9em;
  border-radius: 25px;
  background: #1F6AFF;
  background: linear-gradient(125deg, #1F6AFF 0%, #FD2DEE 100%);
  margin-bottom: .5em; }
  @media (min-width: 800px) {
    .primary-action-large {
      font-size: 1.6em;
      max-width: 9em;
      margin-left: 0; } }

.secundairy-action-large-border {
  border-radius: 25px;
  background: #1F6AFF;
  background: linear-gradient(125deg, #1F6AFF 0%, #FD2DEE 100%);
  margin: auto;
  max-width: 520px;
  padding: 2px;
  margin-bottom: 2.5em; }
  @media (min-width: 800px) {
    .secundairy-action-large-border {
      max-width: 14em;
      margin-left: 0; } }
  .secundairy-action-large-border .secundairy-action-large {
    color: #1F6AFF;
    font-size: 1.8em;
    text-align: center;
    line-height: 1.9em;
    border-radius: 23px;
    background: white; }
    @media (min-width: 800px) {
      .secundairy-action-large-border .secundairy-action-large {
        font-size: 1.6em;
        max-width: 9em; } }

.hero-image-container {
  grid-area: image;
  position: relative; }
  @media (min-width: 800px) {
    .hero-image-container {
      max-width: unset; } }
  .hero-image-container .hero-image {
    width: 100%; }
    @media (min-width: 800px) {
      .hero-image-container .hero-image {
        width: 22em; } }
  .hero-image-container .hero-image-ref {
    font-size: .8em;
    color: #7e7e7e;
    position: absolute;
    bottom: -1.5em;
    right: 0; }
